
.side-nav[data-v-0e47de42] {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    opacity: .9;
    overflow-x: hidden;
    padding-top: 60px;
    -webkit-transition: .5s;
    transition: .5s;
}
.side-nav a[data-v-0e47de42]{
    padding: 10px 10px 10px 30px;
    text-decoration: none;
    font-size: 22px;
    color: #ccc;
    display: block;
    -webkit-transition: .3s;
    transition: .3s;
}
.side-nav a[data-v-0e47de42]:hover {
    color: #fff;
}
.side-nav .btn-close[data-v-0e47de42] {
    position: absolute;
    top: 0;
    right: 22px;
    font-size:36px;
    margin-left:50px;
    cursor: pointer;
}



